  // 
  const [cubeData, setCubeData] = useState<Record<string, string[]>>({});
  const [selectedItems, setSelectedItems] = useState<Record<string, string>>({});
  const [pinnedDimensions, setPinnedDimensions] = useState<Set<string>>(new Set()); // 
